import React from "react";

export const CalendarEvent = ({ event }) => {
  const {
    title,
    user: { name },
  } = event;
  return (
    <div className="d-block p-2">
      <div>
        <strong>{title}</strong>
      </div>
      <div>
        <span>{name}</span>
      </div>
    </div>
  );
};
