import React, { useEffect, useState } from 'react';
//https://www.npmjs.com/package/react-modal
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import './modal.css';
import moment from 'moment';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { uiCloseModal } from '../../actions/ui';
import {
  eventStartAddNew,
  eventClearActive,
  eventStartUpdate,
} from '../../actions/events';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};

Modal.setAppElement('#root');

const startDate = moment().minutes(0).seconds(0).add(1, 'hours');
const endDate = startDate.clone().add(1, 'hours');
const initialEvent = {
  title: 'Evento',
  notes: '',
  start: startDate.toDate(),
  end: endDate.toDate(),
};

export const CalendarModal = () => {
  const [fechaInicio, setFechaInicio] = useState(startDate.toDate());
  const [fechaFin, setFechaFin] = useState(endDate.toDate());
  const [formValues, setFormValues] = useState(initialEvent);
  const [validTitle, setValidTitle] = useState(true);

  const { notes, title, start, end } = formValues;

  const { modalOpen } = useSelector((state) => state.ui); //Redux
  const { activeEvent } = useSelector((state) => state.calendar); //Redux

  const dispatch = useDispatch();

  useEffect(() => {
    if (activeEvent) {
      setFormValues(activeEvent);
    } else {
      setFormValues(initialEvent);
    }
  }, [activeEvent, setFormValues]);

  const closeModal = () => {
    //redux
    dispatch(uiCloseModal());
    dispatch(eventClearActive());
    setFormValues(initialEvent);
  };

  const onFechaInicioChange = (e) => {
    setFechaInicio(e);
    setFormValues({
      ...formValues,
      start: e,
    });
  };

  const onFechaFinChange = (e) => {
    setFechaFin(e);
    setFormValues({
      ...formValues,
      end: e,
    });
  };

  const handleInputChange = ({ target }) => {
    setFormValues({
      ...formValues,
      [target.name]: target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    /* Validaciones */
    const valStart = moment(start);
    const valEnd = moment(end);

    if (valStart.isSameOrAfter(valEnd)) {
      Swal.fire('Error', 'Fecha de fin anterior a fecha de inicio', 'error');
      return;
    }

    if (title.trim().length < 2) {
      setValidTitle(false);
      return;
    }
    setValidTitle(true);

    if (activeEvent) {
      dispatch(eventStartUpdate(formValues));
    } else {
      dispatch(eventStartAddNew(formValues));
    }
    //dispatch(eventSetActive(newEvent));
    closeModal();
  };

  return (
    <Modal
      isOpen={modalOpen}
      onRequestClose={closeModal}
      closeTimeoutMS={200}
      style={customStyles}
      overlayClassName="modal-fondo"
    >
      <h1> {!!activeEvent ? 'Editar evento' : 'Nuevo evento'} </h1>
      <hr />
      <form className="container" onSubmit={handleSubmit}>
        <div className="form-group">
          <label>Fecha y hora inicio</label>
          <DateTimePicker
            onChange={onFechaInicioChange}
            value={fechaInicio}
            className="form-control"
          />
        </div>

        <div className="form-group">
          <label>Fecha y hora fin</label>
          <DateTimePicker
            onChange={onFechaFinChange}
            value={fechaFin}
            minDate={fechaInicio}
            className="form-control"
          />
        </div>

        <hr />
        <div className="form-group">
          <label>Titulo y notas</label>
          <input
            type="text"
            className={`form-control ${!validTitle && 'is-invalid'}`}
            placeholder="Título del evento"
            name="title"
            autoComplete="off"
            value={title}
            onChange={handleInputChange}
          />
          <small id="emailHelp" className="form-text text-muted">
            Una descripción corta
          </small>
        </div>

        <div className="form-group">
          <textarea
            type="text"
            className="form-control"
            placeholder="Notas"
            rows="5"
            name="notes"
            value={notes}
            onChange={handleInputChange}
          ></textarea>
          <small id="emailHelp" className="form-text text-muted">
            Información adicional
          </small>
        </div>

        <button type="submit" className="btn btn-outline-primary btn-block">
          <i className="far fa-save"></i>
          <span> Guardar</span>
        </button>
      </form>
    </Modal>
  );
};
